@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Quicksand);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #f4efee;
  font-family: "Raleway", sans-serif;
  width: 100vw;
}

.Raleway {
  font-family: "Raleway", sans-serif;
}

.Quicksand {
  font-family: Quicksand;
}

.App {
  text-align: center;
  margin: 0 !important;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.fixfooter {
  min-height: 76vh;
}

.container {
  width: 100vw;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.leftBar {
  background-image: url("/images/home.jpg");
  color: #fcffeb;
  min-height: 60vh;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Quicksand;
  text-align: left;
  padding-left: 5%;
}

.header {
  font-size: calc(12px + 7vw);
}

.subheader {
  font-size: calc(12px + 3vw);
}

.fontForm {
  color: #1b2f33;
}

.row {
  border-bottom: 2px solid white;
}

.Home {
  background-color: #f4efee;
  margin: 0;
  padding: 0;
}

.Home-btn {
  background-color: #1b2f33;
  border: none;
  color: white;
}

.Home-btn:hover {
  background-color: #e7eef1;
  color: #1b2f33;
  border: none;
}

.blue {
  margin: 0;
  background-color: #e7eef1;
  color: #1b2f33;
  border-bottom: 2px solid white;

  font-size: calc(12px + 1vh);
}

nav img {
  height: 7vh;
  min-height: 40px;
}

.Navbar-btn {
  background-color: #1b2f33;
  border: none;
  color: white;
}

.Navbar-btn:hover {
  background-color: #e7eef1;
  color: #1b2f33;
  border: none;
}

.Waiting-profilePic {
  width: 40px;
  height: 40px;
  object-fit: cover;
}

.text {
  color: black;
}

.Dashboard .img-fluid {
  width: 100%;
  height: 50vh;
  object-fit: cover;
}

.noborder {
  border-bottom: none;
}

.transparent {
  opacity: 0.8;
  color: #1b2f33;
}

.score {
  border-radius: 50%;
  padding: 1%;
  background-color: #1b2f33;
  color: white;
}

.white-top {
  border-top: 1px solid white;
  padding-top: 2.4vh;
}

.Dashboard-btn {
  background-color: #1b2f33;
  border: none;
  color: white;
}

.Dashboard-btn:hover {
  background-color: #e7eef1;
  color: #1b2f33;
  border: none;
}

.overlaybase {
  position: relative;
}

.overlay {
  position: absolute;
  background-color: #f4efee;
  z-index: 2;
}

@media (max-width: 720px) {
  .container-fluid,
  .Dashboard {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .Dashboard img {
    max-height: 40vh;
  }

  body {
    padding: 0;
  }
}

.Join-img {
  width: 100%;
  height: 60vh;
  max-height: 450px;
  object-fit: cover;
}


.card-img-top {
  width: 100%;
  height: 30vh;
  max-height: 150px;
  object-fit: cover;
}

.add-task {
  background-color: #1b2f33;
  height: 30vh;
  max-height: 150px;
}

.info-icon {
  max-height: 30px;
}

.createLeague {
  margin: auto 10%;
  text-align: center;
}

.Foot {
  background-color: #e7eef1;
  color: #1b2f33;
  border-top: 2px solid white;
}

.card-img-top {
  width: 100%;
  height: 50vh;
  object-fit: cover;
}

.tasksprofile {
  height: 50vw;
}

.profile-pic {
  width: 250px;
  height: 250px;
  object-fit: cover;
}

.notasks {
  margin: auto 35%;
}

@media (max-width: 720px) {
  .profile,
  .container-fluid {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  body {
    padding: 0;
  }
}

